import React, { useState, useEffect, useMemo } from 'react';
import {
  Typography, Box, TextField, FormControl, InputLabel, Select, MenuItem, Chip, Skeleton
} from '@mui/material';
import { FixedSizeList } from 'react-window';
import { Link } from 'react-router-dom';
import { loadBookmarkletData, searchBookmarklets } from '../utils/bookmarkletLoader';
import debounce from 'lodash/debounce';

const bookmarkletCategories = [
  { id: 'productivity', name: 'Productivity Tools', icon: '🚀' },
  { id: 'debugging', name: 'Debugging & Development', icon: '🛠️' },
  { id: 'security', name: 'Security & Privacy', icon: '🔒' }
];

const BookmarkletCard = React.memo(({ bookmarklet, style }) => (
  <Box style={style} sx={{ p: 1 }}>
    <Card
      sx={{
        height: '100%',
        display: 'flex',
        flexDirection: 'column',
        transition: 'transform 0.3s',
        '&:hover': { transform: 'scale(1.05)' },
      }}
      tabIndex={0}
      aria-label={`Bookmarklet: ${bookmarklet.name}`}
    >
      <CardContent sx={{ flexGrow: 1 }}>
        <Typography variant="h6" component="h2">
          {bookmarklet.icon} {bookmarklet.name}
        </Typography>
        <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
          {bookmarklet.description}
        </Typography>
        <Chip label={bookmarklet.category} size="small" sx={{ mr: 1 }} />
      </CardContent>
      <CardActions>
        <Button
          component="a"
          href={bookmarklet.code}
          size="small"
          color="primary"
          draggable="true"
          aria-label={`Drag ${bookmarklet.name} to bookmarks`}
        >
          Drag to Bookmarks
        </Button>
        <Button
          component={Link}
          to={`/bookmarklet/${bookmarklet.id}`}
          size="small"
          aria-label={`Learn more about ${bookmarklet.name}`}
        >
          Learn More
        </Button>
      </CardActions>
    </Card>
  </Box>
));

const HomePage = () => {
  const [bookmarklets, setBookmarklets] = useState([]);
  const [filteredBookmarklets, setFilteredBookmarklets] = useState([]);
  const [loading, setLoading] = useState(true);
  const [searchQuery, setSearchQuery] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('');

  useEffect(() => {
    const fetchData = async () => {
      try {
        const data = await loadBookmarkletData();
        setBookmarklets(data);
        setFilteredBookmarklets(data);
      } catch (error) {
        console.error('Error loading bookmarklets:', error);
      } finally {
        setLoading(false);
      }
    };
    fetchData();
  }, []);

  const debouncedSearch = useMemo(
    () => debounce((query, category) => {
      let result = bookmarklets;
      if (query) result = searchBookmarklets(query, result);
      if (category) result = result.filter(b => b.category === category);
      setFilteredBookmarklets(result);
    }, 300),
    [bookmarklets]
  );

  useEffect(() => {
    debouncedSearch(searchQuery, selectedCategory);
  }, [searchQuery, selectedCategory, debouncedSearch]);

  if (loading) {
    return (
      <Box sx={{ py: 4 }}>
        <Skeleton variant="text" width="30%" height={40} />
        <Box sx={{ mt: 2 }}>
          {[...Array(6)].map((_, i) => (
            <Skeleton key={i} variant="rectangular" height={200} sx={{ mb: 2 }} />
          ))}
        </Box>
      </Box>
    );
  }

  return (
    <Box sx={{ py: 4 }}>
      <Typography variant="h4" component="h1" gutterBottom>
        BookmarkletKit Packager
      </Typography>
      <Box sx={{ display: 'flex', gap: 2, mb: 4 }}>
        <TextField
          fullWidth
          variant="outlined"
          label="Search Bookmarklets"
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
          aria-label="Search bookmarklets"
          inputProps={{ 'aria-label': 'Search bookmarklets' }}
        />
        <FormControl sx={{ minWidth: 200 }}>
          <InputLabel id="category-select-label">Filter by Category</InputLabel>
          <Select
            labelId="category-select-label"
            value={selectedCategory}
            label="Filter by Category"
            onChange={(e) => setSelectedCategory(e.target.value)}
            aria-label="Filter by category"
          >
            <MenuItem value="">All Categories</MenuItem>
            {bookmarkletCategories.map(category => (
              <MenuItem key={category.id} value={category.id}>
                {category.name}
              </MenuItem>
            ))}
          </Select>
        </FormControl>
      </Box>
      {filteredBookmarklets.length === 0 ? (
        <Typography variant="body1" color="text.secondary" sx={{ textAlign: 'center', mt: 4 }}>
          No bookmarklets found matching your criteria.
        </Typography>
      ) : (
        <FixedSizeList
          height={600}
          width="100%"
          itemCount={filteredBookmarklets.length}
          itemSize={250}
          itemData={filteredBookmarklets}
        >
          {({ index, style }) => (
            <BookmarkletCard bookmarklet={filteredBookmarklets[index]} style={style} />
          )}
        </FixedSizeList>
      )}
    </Box>
  );
};

export default HomePage;